import styled from "styled-components";
import gClef from "../../assets/img/g-clef.png";
import wholeNote from "../../assets/img/whole-note.png";

const Wrapper = styled.div`
  position: relative;
  margin-top: 40px;
  margin-bottom: 40px;
`;

const Staff = styled.div`
  width: 500px;
`;

const Line = styled.div`
  height: 1px;
  margin-bottom: 14px;
  background-color: #000;
`;

const Gclef = styled.img`
  position: absolute;
  top: -25px;
  left: 0;
  height: 116px;
`;

const WholeNote = styled.img`
  position: absolute;
top: ${props =>
  props.note == 'C' && '68px' ||
  props.note == 'D' && '61px' ||
  props.note == 'E' && '54px' ||
  props.note == 'F' && '46px' ||
  props.note == 'G' && '39px' ||
  props.note == 'A' && '31px' ||
  props.note == 'B' && '24px'};
  left: ${props =>
  props.index > 0
    ? `${(155 + (35 * props.index))}px`
    : '155px'};
  height: 14px;
`;


const keyOfC = [
  'C', 'D', 'E', 'F', 'G', 'A', 'B'
]

export default class MusicStaff extends React.Component {
  render() {
    return (
      <Wrapper>
        {keyOfC.map((note, index) => {

          console.log(index, note)
          return (
            <WholeNote src={wholeNote} note={note} index={index} />
          );
        })}
        <Gclef src={gClef} />
        <Staff>
          <Line />
          <Line />
          <Line />
          <Line />
          <Line />
        </Staff>
      </Wrapper>
    );
  }
}
